.div__h2{
    font-size: 2em;
    padding: 2em;
    color: white;
}
.div{
    font-family: Arial, sans-serif;
      background-color: #f4f4f4;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: auto;
      background-color: #11164F;
}
.section6__container {
  background: rgb(252,252,252);
  background: linear-gradient(0deg, rgba(252,252,252,1) 0%, rgba(21,103,103,1) 93%);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 350px;
    display: flex;
    flex-direction: column;
  }

.section6__container  form {
    width: 100%;
  }

  .section6__container   label {
    display: block;
    margin-bottom: 8px;
  }

  .section6__container   input, textarea {
    width: 100%;
    padding: 8px;
    margin-bottom: 16px;
    box-sizing: border-box;
  }

  .section6__container  button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .section6__container  button:hover {
    background-color: #45a049;
  }
  /* ---------------------------- */
.section5{
    width: 100%;
    height: 30em;
    background-image: url(../img/contacto.jpg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    
    
}
.section5__h2{
    width: 30em;
    color: rgb(255, 255, 255);
    font-size: 2rem;

    margin-bottom: 1em;
    padding: 1em;
    text-align: center;
    text-shadow: 9px 9px 7px  rgb(0, 0, 0);
}
.section5__p{
    width: 45em;
    background-color: rgba(255, 255, 255, 0.4);
    padding: 1em;
    margin-bottom: 2em;
    font-size: 1.2rem;
   
    
}