import 
.header1{
    font-size: 1rem;
    width: 100%;
    min-height: 30em;
    background-color: #004c64;
}
.header1__img{
    width: 100%;
    height: 25em;
    object-fit: cover;
    object-position: center;
}
.header1__h1{
    text-align: center;
    line-height:2em;
    color: white;
}
/* ------------------------------------------- */

section{
    font-size: 1rem;
    width: 100%;
    display: flex;
}
section .img {
    width: 50%;
    height: 100vh;
    position: sticky;
    top: 0;
    overflow: hidden;
    z-index: -1;
}
section .text {
    width: 50%;
    height: auto;
    padding:2em ;
}
section .text h3{
    font-size: 3em;
    margin: 1rem;
}
section .text p{
    margin-top: .5em;
    font-size: 1.5em;
}
section .text h4{ 
    margin-top: 1em;
    font-size: 2em;
}
/* -------------------------- */
.section{
    position: relative;
    font-size: 1rem;
    width: 100%;
    min-height: 25em;
    background-image: url(../img/dienteConPlaca.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: -1;
}
.section::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(circle, rgba(19,19,19,1) 5%, rgba(251,251,251,0) 88%);
}
.section__div{
    position: relative;
    z-index: 9;
    width: 50em;
    min-height: 20em;
    color: white;
    
}

.section__div--p{
    margin-top: .5em;
}
/*-------------------footer -------------*/
.footer{
    font-size: 1rem;
    width:100%;
    min-height: 5em;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.footer__h2{
    color: white;
}
.footer__p{
    color: white;
}