.header{
    font-size: 1rem;
    width: 100%;
    min-height: 60vh;
    background-image: url(../img/Home1.jpg);
    background-position:top;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: self-end;
    position: relative;
    z-index: -1;
    
}
.header::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(251,251,251,0) 0%, rgba(19,19,19,1) 74%);
}
.header__h2{
    
    font-size: 3em;
    color: white;
    line-height: 1.1em;
    position: relative;
    text-align: center;
}
.header__p{
    width: 39em;
 
    color: rgb(225, 220, 220);
    position: relative;
    text-align: left;
    margin-top: 1em;
    
}
/*------------------section 1--------------*/
.section1{
    font-size: 1rem;
    width: 100%;
    min-height: 30em;
    background-color:white;
    padding: 2em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
  
}
.section1__container{
    width: 40em;
    min-height: 30em;
    height: auto;
    padding-right: 2em;
}
.section1__container-h2{
    text-transform: capitalize;
    color: black;
}
.section1__container-p{
    margin-top: 1em;
}
.section1__imagen{

    width: 35em;
    height: 30em;
    overflow: hidden;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    background: #e0e0e0;
    box-shadow:  20px 20px 60px #bebebe,-20px -20px 60px #ffffff;
}
.section1__imagen-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 50px;
    
}
/* -------------------- section 2 ------------------*/
.section2{
    font-size: 1rem;
   
   width: 100%;
   min-height:100em;
   margin: 0 auto;
   background-color: #dbdbdb;
   position: relative;

   display: flex;
   flex-direction: column;
   
 
}

.section2__container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
}
.section2__cabecera{
    width: 100%;

}
.section2__cabecera__titulo{
    color: rgb(203, 1, 1);
    text-align: center;
    text-transform: capitalize;
    font-size: 30px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    padding:1em 0 .5em 0;
}
.section2__card{
    position: relative;
 
    box-sizing: border-box;
    border: 1px solid blue;
    width: 25em;
    min-height: 27em;
    margin-top:19px;
    margin-right:15px;
    margin-left:10px;
    
    padding: 10px;
  
    overflow: hidden;
    background-color: rgb(34, 34, 63);
    box-shadow: 10px 8px 11px black;
    color: white;
    text-align: center;  
    
    
}
.section2__card__imagen{
   
    display: block;
    width: 100%;
    height: 200px;
    object-fit: cover;
}
.section2__card__nombre{
    text-transform: uppercase;
}
.section2__card::before{
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    box-sizing: border-box;
    width: 100%;
    height: 4px;
    border-left: 4px solid rgb(70, 153, 51);/*border-left: 4px solid transparent;*/
    border-bottom: 2px solid orange;/*border-left: 4px solid transparent;*/
    
    
    transform: translateX(80%);
    
}

.section2__card:hover::before{
   
    border-color: red;
    transform: translateX(0);
    height: 100%;
    /*tiempo*/transition: .5s transform linear,.5s height linear .5s ;
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
}
/*-------------------footer -------------*/
.footer{
    font-size: 1rem;
    width:100%;
    min-height: 5em;
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.footer__h2{
    color: white;
}
.footer__p{
    color: white;
}