.navbar {
    position: relative;
    z-index: 0;
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    -webkit-transition: 350ms;
    -moz-transition: 350ms;
    -ms-transition: 350ms;
    -o-transition: 350ms;
}
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px; 
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
  .padre{
    position: relative;
  }
  .logo{
    position: absolute;
    top: 10px;
    right: 70px;
    color:white;

  }
  @media (max-width: 803px) {
    .logo{
      font-size: 1.5em;
      line-height: 40px;
    }
  }
  @media (max-width: 633px) {
    .logo{
      width: 8em;

      font-size: 1.5em;
      line-height: 40px;
    }
    .navbar {
      background-color: #060b26;
      height: 150px;
      display: flex;
      justify-content: start;
      align-items: center;
    }
    .nav-menu {
      width: 250px;
    }
    .nav-text {
      padding: 8px 0px 8px 0px;
    }
    .nav-text a {
     
      padding:0;
      border-radius: 4px;
    }
  }
  