.header3{
    font-size: 1rem;
    width: 100%;
    height: 60vh;
    overflow: hidden;
    position: relative;
    z-index: -1;
}
.header3__img{
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}
.header3::before{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(circle, rgba(252,252,252,0) 0%, rgba(49,52,52,1) 93%);
    width: 100%;
    height: 100%;
}
.header3__h2{
    position: absolute;
    top:1em;
    left: 1em;

    text-transform: capitalize;
    font-size: 3em;
    color: #010101;
    text-shadow: 0 1px 0px #c3c3c3, 1px 0 0px #c3c3c3, 1px 2px 1px #c3c3c3, 2px 1px 1px #c3c3c3, 2px 3px 2px #c3c3c3, 3px 2px 2px #c3c3c3, 3px 4px 2px #c3c3c3, 4px 3px 3px #c3c3c3, 4px 5px 3px #c3c3c3, 5px 4px 2px #c3c3c3, 5px 6px 2px #c3c3c3, 6px 5px 2px #c3c3c3, 6px 7px 1px #c3c3c3, 7px 6px 1px #c3c3c3, 7px 8px 0px #c3c3c3, 8px 7px 0px #c3c3c3;

}
/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
.container{
    background: linear-gradient(0deg, rgba(252,252,252,1) 0%, rgba(21,103,103,1) 93%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    padding: 40px 0;
    position: relative;
    
 
}
/*---- caja estatica*/
.container .card{

    /*background-color: green;*/
    position: relative;

    width: 320px;
    min-height: 490px;
    border-radius: 15px;
    margin: 30px;
    box-shadow: inset 6px 6px 6px rgba(0,0,0,0.05),
     inset -6px -6px 6px rgba(255,255,255,0.5), 
     6px 6px 6px rgba(0,0,0,0.05), 
     -6px -6px 6px rgba(255,255,255,0.5);

}
/*---------caja dentro----------*/
.container .card .box{
    position: absolute;
    top: 20px;/*reduce como padding*/
    left: 20px;
    right: 20px;
    bottom: 20px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    border-radius: 15px;
    background-color: rgb(219, 212, 212);
    /*al container lo centra*/
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.5s;
}
.container .card:hover .box{
    transform: translateY(-50px);
    box-shadow: 0 10px 20px rgba(0,0,0,0.5);
    background: rgb(131,58,180);
background: linear-gradient(232deg, rgba(131,58,180,1) 29%, rgba(253,29,29,1) 80%, rgba(252,176,69,1) 100%);

}
/*-----------------------------*/
.container .card .box .content{
    padding: 20px;
    text-align: center;
}
/*los numeros----------------------------*/
.container .card .box .content h2{
    position: absolute;
    z-index: 0;
    top: -10px;
    right: 30px;
    font-size: 8em;
    color: rgba(0, 0, 0,0.03);
    transition: 0.5s;
    pointer-events: none;/*No selccionar texto*/
}
/*-------------------------*/
/*contenido*/
.container .card:hover .box .content h2{
    color: rgba(0, 0, 0,0.08);
}
.container .card .box .content h3{
    font-size: 1.8em;
    color: #5e1111;
    z-index: 1;
    transition: 0.5s;
}
.container .card .box .content p{
    font-size: 1em;
    font-weight: 300;
    color: #040404;
    z-index: 1;
    transition: 0.5s;
    
}
.container .card .box .content a{
    display: inline-block;
    position: relative;
    padding: 8px 20px;
    background-color: rgb(92, 179, 216);
    margin-top: 15px;
    border-radius: 20px;
    color: #fff;
    text-decoration: none;
    font-weight: 400;
    box-shadow: 0 10px 20px rgba(0,0,0,0.2);
}
.container .card:hover .box .content a{
    background-color: rgb(237, 123, 30);
}
.container__h2{
    width: 100%;
    padding: 1em;
    text-align: center;
    font-size: 2rem;
    color: rgb(0, 0, 0);
}
/* ++++++++++++++++++++++++++++++++++++++ */
.section4{
    width: 100%;
    min-height: 20em;
    background-image: url(../img/dentalSolution.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    padding: .5em;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
}
.section4__h3{
    font-size: 1.6em;
    background-color: rgba(255, 255, 255,.3);
}
.section4__p{
    font-weight: 400;
    background-color: rgba(255, 255, 255,.3);
    font-size: 1.5em;
    
   
}


